* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

body {
  font-family: Arial, sans-serif;
  background-color: #f0f2f5;
  padding: 16px;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
}

#root {
  flex: 1;
  overflow-y: auto;
}

input {
  border: 1px solid #ccc;
  border-radius: 4px;
  padding: 8px;
  font-size: 16px;
}

input:focus {
  outline: none;
  border-color: #007bff;
}

label {
  display: block;
  margin-bottom: 10px;
}

h1 {
  text-align: center;
  margin-bottom: 20px;
}

@media (max-width: 480px) {
  body {
    padding: 8px;
  }

  input {
    font-size: 14px;
    padding: 6px;
  }
}
